import facebook from './../../img/footer/facebook.svg';
import github from './../../img/footer/github.svg';
import linkedin from './../../img/footer/linkedin.svg';
import twitter from './../../img/footer/twitter.svg';
import web from './../../img/footer/web.svg';

const defaultImg = [
    facebook,
    github,
    linkedin,
    twitter,
    web,
]
const MedsosLink = ({imgs=defaultImg, style={}}) => {
  return (
    <div className='flex gap-6' style={style}>
        {imgs.map((icon, idx)=>(
            <img className='cursor-pointer' src={icon} alt='img' key={idx} style={{width:'24px'}}/>
        ))}
    </div>
  )
}

export default MedsosLink