import linkedinimg from './../../img/tutors/linkedin.svg';
import tweeter from './../../img/tutors/tweeter.svg';
import defaultPhoto from './../../img/tutors/Avatar1.jpg'

const TutorCard = ({img=defaultPhoto, name='Theresa Webb', expertise='Application Support Analyst Lead', experience='Former co-founder of Opendoor. Early staff at Spotify and Clearbit.', twitter, linkedin}) => {
    const containerStyle = {
        display: 'flex',
        flexDirection: 'column',
        padding: '24px',
        width: '276px',
        height: '340px',
        background: '#F9FAFB',
    } 
    const nameStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '18px',
        lineHeight: '28px',
        color: '#101828',
    }

    const expertiseStyle = {
        ...nameStyle,fontWeight:'400', fontSize:'16px', lineHeight:'24px', color:'#6941C6'
    }

    const experienceStyle = {
        ...expertiseStyle, color:'#667085'
    }

  return (
    <div className='items-center text-center' style={containerStyle}>
        <div className='overflow-hidden rounded-full flex self-center mb-5' style={{width:'80px',height:'80px'}}>
            <img src={img} alt='img'/>
        </div>
        <div style={nameStyle}>{name}</div>
        <div className='mb-2' style={expertiseStyle}>{expertise}</div>
        <div style={experienceStyle}>{experience}</div>
        <div className='flex items-center gap-4 mt-auto'>
            <img src={tweeter} alt='img'/>
            <img src={linkedinimg} alt='img'/>
        </div>
    </div>
  )
}

export default TutorCard