import avatarDefault from './../../img/testimony/Avatar1.jpg'
const UserTestimonyProfile = ({img=avatarDefault, name='Jacob Jones', institution='Student, National Univerity'}) => {
  const nameStyle = {
    fontFamily: 'Inter',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '18px',
    lineHeight: '28px',
    textAlign: 'center',
    color: '#101828',
  }

  const institutionStyle = {
    ...nameStyle,fontWeight:'400',fontSize:'16px',lineHeight:'24px', color:'#667085'
  }
  return (
    <div className='w-full flex flex-col items-center'>
      <div className='flex justify-center rounded-full overflow-hidden mb-4' style={{width:'64px', height:'64px'}}><img src={img} alt='img' style={{width:'64px'}}/></div>
      <div className='mb-1' style={nameStyle}>{name}</div>
      <div style={institutionStyle}>{institution}</div>
    </div>
  )
}

export default UserTestimonyProfile;