import Caption from "./Caption";
import imgDefault from './../../img/blog/sampleBlog1.jpg';
// import {useRef} from 'react';

const BlogCard = ({fullSize=false, img=imgDefault, date, title, text, tags}) => {
  return (
    <div className={"flex gap-6"} style={{maxWidth:'600px', flexDirection:fullSize?"column":'row'}}>
        <div className="overflow-hidden relative" style={{width:fullSize?'100%':'50%', height:'200px'}}>
            <img className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2" src={img} alt='img' style={{width:fullSize?'100%':'auto',height:fullSize?'auto':'100%', minWidth:fullSize?'auto':'300px'}}/>
        </div>
        <Caption title={title} date={date} text={text} tag={tags} height={200} style={{width:fullSize?'100%':'50%'}}/>
    </div>
  )
}

export default BlogCard;