import React from 'react'

const defaultLinks = [
    <div>Overview</div>,
    <div>Features</div>,
    <div>Solutions</div>,
    <div>Tutorials</div>,
    <div>Pricing</div>
]
const LinkGroup = ({title='Product', links=defaultLinks}) => {
    const titleStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '14px',
        lineHeight: '20px',
        color: '#98A2B3',
    }
    const linkStyle = {
        ...titleStyle, fontWeight:'500', fontSize:'16px',lineHeight:'24px', color:'#EAECF0'
    }
  return (
    <div>
        <div className='mb-4' style={titleStyle}>{title}</div>
        <div className='flex flex-col gap-3' style={linkStyle}>
            {links}
        </div>
    </div>
  )
}

export default LinkGroup