import searchimg from './../../img/header/Search.svg'
import DropDown from './DropDown';
import {useState} from 'react';

export default function Search({placeHodler='Want to learn?',...rest}){
    const [isActive, setActive] = useState(false);

    const formStyle={
        width: '367px',
        height: '44px',
        background: '#FFFFFF',
        border: '1px solid #D0D5DD',
        boxShadow: '0px 1px 2px rgba(16, 24, 40, 0.05)',
        borderRadius: '8px'
    }
    return(
        <form style={formStyle} className='flex items-center justify-between'>
            <div style={{height:'44px'}} className='flex items-center ml-4'>
                {isActive?undefined:<img src={searchimg} alt='img'/>}
                <input type='text' className='ml-2' placeholder={placeHodler} onFocus={()=>{setActive(true)}}  onBlur={()=>{setActive(false)}}/>
            </div>
            <DropDown className="mx-1"/>
        </form>
    );
}