import BlogCard from "./BlogCard";
import sampleBlog2 from './../../img/blog/sampleBlog2.jpg';
import sampleBlog3 from './../../img/blog/sampleBlog3.jpg';
import TagLabel from "../low-level/TagLabel";
import { cloneElement } from "react";

const defaultCard = [
    <BlogCard/>,
    <BlogCard img={sampleBlog2} date={'September 24, 2017'} title={'UX Mapping Methods'} text={'Visual-design principles can be applied consistently throughout the process of creating a polished UX map...'} tags={[
        <TagLabel text='Research' category='research'/>,
        <TagLabel text='UI Design' category='uidesign'/>
    ]}/>,
    <BlogCard img={sampleBlog3} date={'March 13, 2014'} title={'Agile Development Projects and Usability'} text={'Agile methods aim to overcome usability barriers in traditional development, but post new threats to user experience quality. '} tags={[
        <TagLabel text='Research' category='research'/>,
        <TagLabel text='Programming' category='programming'/>,
        <TagLabel text='Developments' category='development'/>
    ]}/>
]
const BlogContainer = ({cards=defaultCard}) => {
    const halfSize = [...cards];
    halfSize.pop();
    const fullSize = cloneElement(cards[cards.length - 1],{
        fullSize:true
    });
  return (
    <div className="flex justify-between">
        <div className="flex flex-col justify-between">{halfSize}</div>
        {fullSize}
    </div>
  )
}

export default BlogContainer