import CardThumbnail from "./CardThumbnail";
import figma from './../../img/programe/figma.jpg'
import Rating from "./Rating";
import TutorsName from "./TutorsName";
import arrowIcon from './../../img/programe/arrow.svg'
import avatar1 from './../../img/programe/Avatar1.jpg'

const ProgramCard = ({img=figma,duration='08 hr 12 mins',category='Design',title='Figma UI UX Design..',description='Use Figma to get a job in UI Design, User Interface, User Experience design.',rate=4.4, tutor='Jane Cooper', tutorImg=avatar1, price='17.84'}) => {

  const cardStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    padding: '24px 24px 32px',
    width: '384px',
    height: '553px',
    gap: '12px',
    background: '#FFFFFF',
    boxShadow: '0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03)',
    borderRadius: '8px',
  }

  const categoryStyle = {
    fontFamily: 'Inter',
    fontStyle: 'normal',
    fontWeight: '600',
    fontSize: '14px',
    lineHeight: '20px',
    color: '#6941C6',
  }

  const titleStyle = {
    ...categoryStyle, fontSize:'24px', lineHeight:'32px', color:'#101828'
  }

  const descriptionStyle = {
    ...categoryStyle, fontSize:'16px', fontWeight: '400', lineHeight:'24px', color:'#667085'
  }

  const priceStyle = {
    ...categoryStyle, fontSize:'26px', fontWeight: '700', lineHeight:'31px', color:'#7F56D9'
  }

  return (
    <div style={cardStyle}>
        <CardThumbnail img={img} duration={duration}/>
        <div style={categoryStyle}>{category}</div>
        <div className="w-full flex items-center justify-between">
          <div style={titleStyle}>{title}</div>
          <img src={arrowIcon} alt='img' style={{width:'10px'}}/>
        </div>
        <div style={descriptionStyle}>{description}</div>
        <Rating rating={rate}/>
        <div className="w-full flex items-center justify-between mt-auto">
          <TutorsName name={tutor} img={tutorImg}/>
          <div style={priceStyle}>${price}</div>
        </div>
        
    </div>
  )
}

export default ProgramCard