import speak from './../../img/home/speak.svg'
export default function Label({img=speak,text='Public Speaking'}){
    const textStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '16px',
        color: 'rgba(29, 41, 57, 0.7)',
    }
    return(
        <div className='flex items-center'>
            <img src={img} alt='img' style={{width:'32px'}} className='mr-3'/>
            <div style={textStyle}>{text}</div>
        </div>
    )
}