export default function SuperPrimaryBtn({text='Sample Btn', ...rest}){
    const style = {
        height: '60px',
        background: "#7F56D9",
        border: '1px solid #7F56D9',
        boxShadow: '0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px #F4EBFF',
        borderRadius: '8px',
        lineHeight: '40px',
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '18px',
        color: '#FFFFFF'
    }
    return(
        <button style={style} className='w-fit px-4' {...rest}>{text}</button>
    );
}