import photoDefault from './../../img/home/sample model.png'
export default function ModelFrame({photo=photoDefault, style={}}){
    const imgBackgroundStyle = {
        width: '495px',
        height: '495px',
        background: '#7F56D9',
        border: '0.765px solid #7F56D9',
        borderRadius: '688.5px',
    }
    const bigCircle = {
        width: '495.72px',
        height: '495.72px',
        left: '0px',
        top: '0px',
        border: '0.765px solid #7F56D9',
        borderRadius: '688.5px',
    }
    const smallCircle = {
        width: '42.84px',
        height: '42.84px',
        left: '50px',
        top: '475px',
        background: '#7F56D9',
        border: '0.765px solid #7F56D9',
        borderRadius: '688.5px'
    }
    return(
        <div className='relative' style={{...style,height:'520px', width:'520px'}}>
            <div className='absolute z-10' style={{left:'25px', top:'25px'}}>
                <div className='relative flex justify-center overflow-hidden' style={imgBackgroundStyle}>
                    <img className='absolute bottom-0' src={photo} alt='img' style={{width:'366px'}}/>
                </div>
            </div>
            <div className='absolute' style={bigCircle}/>
            <div className='absolute' style={smallCircle}/>
        </div>
 
    );
}