import Logo from './Logo';
import PrimaryBtn from './../low-level/PrimaryBtn';
import LinkBtn from './../low-level/LinkBtn';
import Search from './Search';

export default function Header({menus=['Menu 1','Menu 2', 'Menu 3']}){
    return(
        <div className='w-screen z-50' style={{backgroundColor:'#FBFEFD', position:'sticky', top:'0'}}>
            <div style={{height:'100px'}} className='flex w-full items-center gap-x-7 mx-auto max-w-7xl'>
                <Logo/>
                <Search/>
                {menus.map((menu, idx)=>(
                    <LinkBtn key={idx}>{menu}</LinkBtn>
                ))}
                <div className='flex items-center ml-auto gap-x-7'>
                    <LinkBtn>Sign in</LinkBtn>
                    <PrimaryBtn text='Create free account'/>
                </div>
            </div>
        </div>
    );
}