import clock from './../../img/programe/clock.svg';
import sampleImg from './../../img/programe/figma.jpg'
const CardThumbnail = ({img=sampleImg,duration='08 hr 12 mins'}) => {
    const thumbnailStyle = {
        width: '336px',
        height: '240px',
        borderRadius: '4px',
    }
    const durationStyle = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '6px 10px',
        gap: '6px',
        position: 'absolute',
        background: '#FFFFFF',
        backdropFilter: 'blur(2px)',
        borderRadius: '4px',
    }
  return (
    <div className='relative' style={thumbnailStyle}>
        <img src={img} alt='img'/>
        <div className='top-3 right-3' style={durationStyle}>
            <img src={clock} alt='img' style={{width:'16px'}}/>
            <div>{duration}</div>
        </div>
    </div>
  )
}

export default CardThumbnail