//sample logo colaboration
import codecov from './../../img/home/codecov.svg';
import duolingo from './../../img/home/duolingo.svg'
import magic_leap from './../../img/home/magic_leap.svg'
import user_testing from './../../img/home/user_testing.svg'

export default function Colaboration ({logos=[codecov,duolingo,magic_leap,user_testing],numberColaboration='250'}){
    const numberStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '30px',
        color:'#7F56D9'
    }
    const collaborationTextStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '300',
        fontSize: '25px',
        color:'#101828'
    }
    return(
        <div className='flex w-full items-center my-12'>
            <div style={{width:'178px'}}>
                <div style={numberStyle}>{numberColaboration}+</div>
                <div style={collaborationTextStyle}>Collaboration</div>
            </div>
            <div className='flex justify-evenly w-full '>
                {logos.map((src, idx)=>(
                    <img src={src} key={idx} alt='img' style={{height:'36px'}}/>
                ))}
            </div>
        </div>
    );
}