import BackgroundImage from "./BackgroundImage";

export default function HomeBackground({containerMaxWidth='80rem', containerMinWidth='60rem', children}){
    return(
        <div className='w-screen' style={{backgroundColor:'rgba(36, 187, 140, 0.02)', minHeight:'40rem', paddingTop:'47px', paddingBottom:'43px'}}>
            <div className='relative mx-auto' style={{maxWidth:'110rem'}}>
                <BackgroundImage style={{position:'absolute',right:'0px', bottom:'10px'}}/>
                <BackgroundImage style={{position:'absolute',left:'0px', top:'10px', transform: 'rotate(180deg)'}}/>
                <div style={{maxWidth:containerMaxWidth, minWidth:containerMinWidth}} className='mx-auto'>
                    {children}
                </div>
            </div>
        </div>
    );
}