export default function DropDown({options=['Option 1','Option 2', 'Option 3'], ...rest}){
    const dropDownStyle = {
        width: "111px",
        height: "36px",
        background: "#F9F5FF",
        borderRadius: "8px",
        color: "#7F56D9",
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '14px',
        lineHeight: '36px',    
    }
    return(
        <select style={dropDownStyle} name='targetSearch' id='search' {...rest}>
            {options.map((data, idx)=>(
                <option key={idx} value={data}>{data}</option>
            ))}
        </select>
    );
}