//default img
import Progress_circle from './../../img/home/Progress circle.svg';

export default function CardTypeB({img=Progress_circle,text1='5K+',text2='Online Courses', boldTop=true, style={}}){
    const containerStyle = {
        background: '#F5F5F4',
        border: '0.765px solid #7F56D9',
        boxShadow: '0px 18.36px 38.25px rgba(0, 0, 0, 0.15)',
        borderRadius: '18.36px'
        
    }

    const boldStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '25px',
        color: '#101828',
    }

    const lightStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '15px',
        color: '#101828',
        opacity: '0.5',
    }

    return(
        <div className='flex flex-col h-48 items-center px-4 py-5 w-fit gap-4' style={{...containerStyle,...style}}>
            <img src={img} alt='img' style={{width:'72px'}}/>
            <div className='flex flex-col items-center'>
                <div className='mb-3' style={boldTop?boldStyle:lightStyle}>{text1}</div>
                <div style={boldTop?lightStyle:boldStyle}>{text2}</div>
            </div>
        </div>
    );
}