import CardServices from "./CardServices";
import uxIcon from './../../img/services/ux.svg';
import marketing from './../../img/services/marketing.svg'
import small_circle from './../../img/home/small_circle.svg';

const defaultCards = [
    <CardServices img={uxIcon} title={'User Experience'} description={'Lessons on design that cover the most recent developments.'} color={'#F4EBFF'}/>,
    <CardServices/>,
    <CardServices img={marketing} title={'Marketing'} description={'Marketing courses that cover the most recent marketing trends'} color={'#FCE7F6'}/>
]

const CardServicesSweeper = ({cards=defaultCards}) => {
  return (
    <div className="py-10">
        <div className="w-full flex pb-10 justify-between">
            {cards}
        </div>
        <div className="flex justify-center gap-3">
            <img src={small_circle} alt='img'/>
            <img src={small_circle} alt='img'/>
            <img src={small_circle} alt='img'/>
        </div>
    </div>

  )
}

export default CardServicesSweeper