import LinkGroup from './LinkGroup'
import TagLabel from '../low-level/TagLabel'

const FooterLink = () => {
  return (
    <div className='w-full flex justify-between'>
        <LinkGroup/>
        <LinkGroup title='Company' links={
            [<div>About us</div>,
            <div>Careers</div>,
            <div>Press{<TagLabel text='new' style={{marginLeft:'8px'}}/>}</div>,
            <div>News</div>
            ]
        }/>
        <LinkGroup title='Social' links={
            [<div>Twitter</div>,
            <div>LinkedIn</div>,
            <div>GitHub</div>,
            <div>Dribbble</div>
            ]
        }/>
        <LinkGroup title='Legal' links={
            [<div>Terms</div>,
            <div>Privacy</div>,
            <div>Cookies</div>,
            <div>Contact</div>
            ]
        }/>                
    </div>
  )
}

export default FooterLink