import Logo from "./component/header/Logo";
import DropDown from "./component/header/DropDown";
import Search from "./component/header/Search";
import PrimaryBtn from "./component/low-level/PrimaryBtn";
import LinkBtn from "./component/low-level/LinkBtn";
import Header from "./component/header/Header";
import BackgroundImage from "./component/home/BackgroundImage";
import Colaboration from "./component/home/Colaboration";
import CardTypeA from "./component/home/CardTypeA";
import CardTypeB from "./component/home/CardTypeB";
import ModelFrame from "./component/home/ModelFrame";
import HomeMainImage from "./component/home/HomeMainImage";
import Label from "./component/home/Label";
import Home from "./component/home/Home";
import board from './img/home/board.svg';
import SuperPrimaryBtn from "./component/low-level/SuperPrimaryBtn";
import HomeText from "./component/home/HomeText";
import HomeBackground from "./component/home/HomeBackground";
import CardServices from "./component/services/CardServices";
import Services from "./component/services/Services";
import CardServicesSweeper from "./component/services/CardServicesSweeper";
import ProgramCard from "./component/program/ProgramCard";
import CardThumbnail from "./component/program/CardThumbnail";
import Rating from "./component/program/Rating";
import TutorsName from "./component/program/TutorsName";
import CardProgramSweeper from "./component/program/ProgramsSweeper";
import Program from "./component/program/Program";
import SecondaryBtn from "./component/low-level/SecondaryBtn";
import TutorCard from "./component/tutors/TutorCard";
import TutorSlider from "./component/tutors/TutorSlider";
import Tutors from "./component/tutors/Tutors";
import UserTestimonyProfile from "./component/testimony/UserTestimonyProfile";
import TestimonyCard from "./component/testimony/TestimonyCard";
import Testimony from "./component/testimony/Testimony";
import TagLabel from "./component/low-level/TagLabel";
import Caption from "./component/blog/Caption";
import BlogCard from "./component/blog/BlogCard";
import BlogContainer from "./component/blog/BlogContainer";
import Blog from "./component/blog/Blog";
import LinkGroup from "./component/footer/LinkGroup";
import FooterLink from "./component/footer/FooterLink";
import MedsosLink from "./component/footer/MedsosLink";
import Footer from "./component/footer/Footer";

function Section({name="This is Section"}){
    return(
        <div className="text-3xl font-bold">Section: {name}</div>
    );
}
function Title({title="This is Title"}){
    return(
        <div className="text-lg font-medium">Component of: {title}</div>
    );
}

export default function Discovery(){
    return(
        <>
        <div className="max-w-7xl mx-auto">
            {/* Common Component Section */}
            <Section name="Common Component"/>
            <Title title="Super Btn"/>
            <SuperPrimaryBtn/>
            <Title title="Primary Btn"/>
            <PrimaryBtn/>
            <Title title="Secondary Btn"/>
            <SecondaryBtn/>
            <Title title="Link Btn"/>
            <LinkBtn>Tester</LinkBtn>
            <Title title="Tag Label"/>
            <TagLabel/>
            <br/>

            {/* Header Section */}
            <Section name="Header"/>
            {/* Header Logo */}
            <Title title="Logo"/>
            <Logo onClick={()=>{console.log('Clicked')}}/>
            <Title title="DropDown"/>
            <DropDown/>
            <Title title="Search"/>
            <Search/>
            <Title title="Header"/>
            <Header menus={['Program','Enterprise','Universities']}/>
            <br/>

            {/* Home Section */}
            <Section name="Home"/>
            <Title title="Background Image"/>
            <BackgroundImage/>

            <Title title="Colaboration"/>
            <Colaboration/>

            <Title title="Card Type A"/>
            <CardTypeA/>
            <CardTypeA img={board} boldTop={false} text1='Tutors' text2="250+"/>
            <Title title="Card Type B"/>
            <CardTypeB/>
            <Title title="Model Frame"/>
            <ModelFrame/>
            <Title title="Home Main Image"/>
            <HomeMainImage/>
            <Title title="Label"/>
            <Label/>
            <Title title="Home Text"/>
            <HomeText/>
            <Title title="Home Section"/>
            <Home/>
        </div>
        <Title title="Background Color"/>
        <HomeBackground/>
        <Title title="Final Home Section"/>
        <HomeBackground><Home/></HomeBackground>
        
        <div className="max-w-7xl mx-auto">
            {/* Home Section */}
            <Section name="Services"/>
            <Title title="Card Services"/>
            <CardServices/>
            <Title title="Cards Sweeper"/>
            <CardServicesSweeper/>
            <Title title="Services Section"/>
            <Services/>

            {/* Home Section */}
            <Section name="Program Section"/>
            <Title title="Thumbnail"/>
            <CardThumbnail/>
            <Title title="Rating"/>
            <Rating/>
            <Title title="Tutors Profile"/>
            <TutorsName/>
            <Title title="Program Card"/>
            <ProgramCard/>
            <Title title="Program Card Sweeper"/>
            <CardProgramSweeper/>
            <Title title="Program section"/>
            <Program/>

            {/* Home Section */}
            <Section name="Tutors Section"/>
            <Title title="Tutors Card"/>
            <TutorCard/>
            <Title title="Tutors Slider"/>
            <TutorSlider/>
            <Title title="Tutors Section"/>
            <Tutors/>

            {/* Home Section */}
            <Section name="Testimony Section"/>
            <Title title="Testimony profile"/>
            <UserTestimonyProfile/>
            <Title title="Testimony Card"/>
            <TestimonyCard/>
            <Title title="Testimony Section"/>
            <Testimony/>

            {/* Home Section */}
            <Section name="Blog Section"/>
            <Title title="Caption"/>
            <Caption/>
            <Title title="Blog Card"/>
            <BlogCard/>
            <BlogCard fullSize={true}/>
            <Title title="Blog Container"/>
            <BlogContainer/>
            <Title title="Blog Container"/>
            <Blog/>

            {/* Home Section */}
            <Section name="Footer Section"/>
            <Title title="Link Group"/>
            <LinkGroup/>
            <Title title="Footer Link"/>
            <FooterLink/>
            <Title title="Medsos"/>
            <MedsosLink/>
            <Title title="Footer"/>
        </div>
        <Footer/>
        </>
    );
}