import ProgramCard from './ProgramCard';
import small_circle from './../../img/home/small_circle.svg';

//card data test 2
import learn_with from './../../img/programe/learn_with.jpg';
import avatar2 from './../../img/programe/Avatar2.jpg';

import buildUi from './../../img/programe/buildUi.jpg';
import avatar3 from './../../img/programe/Avatar3.jpg';


const defaultCards = [
    <ProgramCard/>,
    <ProgramCard img={learn_with} duration={'06 hr 3 mins'} category={'Programming'} title="Learn With Shoaib" description='Design Web Sites and Mobile Apps that Your Users Love and Return to Again.' rate={4.6} tutor={'Jenny Wilson'} tutorImg={avatar2} price={'8.99'}/>,
    <ProgramCard img={buildUi} duration={'01 hr 2 mins'} category={'Design'} title="Building User Interface.." description='Learn how to apply User Experience (UX) principles to your website designs.' rate={4.2} tutor={'Esther Howrad'} tutorImg={avatar3} price={'11.70'}/>
]

const CardProgramSweeper = ({cards=defaultCards}) => {
  return (
    <div className="py-10">
        <div className="w-full flex pb-10 justify-between">
            {cards}
        </div>
        <div className="flex justify-center gap-3">
            <img src={small_circle} alt='img'/>
            <img src={small_circle} alt='img'/>
            <img src={small_circle} alt='img'/>
        </div>
    </div>

  )
}

export default CardProgramSweeper