import BlogContainer from "./BlogContainer"

const Blog = () => {
    const style = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '24px',
        lineHeight: '32px',
        color: '#101828',
    }
  return (
    <div className="my-20">
        <div className="mb-8" style={style}>Our recent blogs</div>
        <BlogContainer/>
    </div>
  )
}

export default Blog