import star_active from './../../img/programe/star_active.svg';
import star_inactive from './../../img/programe/star_inactive.svg';

const Rating = ({rating=3.4, participant=12304}) => {
    const ratingStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '14px',
        lineHeight: '17px',
        color: '#7F56D9',
        marginRight:'7px'
    }

    const participantStyle = {
        ...ratingStyle, fontWeight:'400', color:'#969696',marginLeft:'10px'
    }

    const ratingRound = Math.round(rating);
    const stars = [];
    for(let i = 1; i<6; i++){
        const starType = 
        <img src={i<ratingRound+1?star_active:star_inactive} alt='img' style={{width:'16px'}}/>
        stars.push(starType)
    }
  return (
    <div className='flex items-center'>
        <div style={ratingStyle}>{rating}</div>
        {stars}
        <div style={participantStyle}>{'('}{participant.toLocaleString('en-US')}{')'}</div>
    </div>
  )
}

export default Rating