export default function SecondaryBtn({text='Sample Btn', style={}, ...rest}){
    const buttonStyle = {
        height: '40px',
        background: "#F9FAFB",
        border: '1px solid #D0D5DD',
        boxShadow: '0px 1px 2px rgba(16, 24, 40, 0.05)',
        borderRadius: '8px',
        lineHeight: '40px',
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '14px',
        color: '#101828'
    }
    return(
        <button style={{...style,...buttonStyle}} className='w-fit px-4' {...rest}>{text}</button>
    );
}