import big_circle from './../../img/home/big_circle.svg';
import small_circle from './../../img/home/small_circle.svg';

export default function BackgroundImage({left=30, top=315, style={}}){
    const smallCirclePosition = {
        left:left,
        top:top
    }
    return(
        <div style={style} className='-z-30'>
            <div className='relative'>
                <img src={big_circle} alt='img'/>
                <img style={smallCirclePosition} src={small_circle} className='absolute' alt='img'/>
            </div>
        </div>
    );
}