import SuperPrimaryBtn from "../low-level/SuperPrimaryBtn";
import Label from "./Label";
import briefcase from './../../img/home/briefcase.svg';
import idea from './../../img/home/idea.svg'

export default function HomeText(){
    const mainTextStyle={
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '64px',
        letterSpacing: '-0.5px',
        lineHeight: '82px',
        textTransform: 'capitalize',
        color:'#101828'
    }

    const purpleTextStyle={...mainTextStyle,color:'#7F56D9'}

    const descriptionStyle={
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '16px',
        lineHeight: '180%',
        color: '#646464',
    }
    return(
        <div className="w-full" style={{marginTop:'100px'}}>
            <div style={mainTextStyle}>Up Your <span style={purpleTextStyle}>Skills</span> To <span style={purpleTextStyle}>Advance</span> Your <span style={purpleTextStyle}>Career</span> Path</div>
            <div style={descriptionStyle} className='my-5'>Provides you with the latest online learning system and material that help your knowladge growing.</div>
            <div className="flex gap-7 my-9">
                <SuperPrimaryBtn text="Get Started"/>
                <SuperPrimaryBtn text="Get Free Trial"/>
            </div>
            <div className="flex gap-7 my-9">
                <Label/>
                <Label img={briefcase}/>
                <Label img={idea}/>
            </div>
        </div>
    );
}