import SecondaryBtn from "../low-level/SecondaryBtn";
import CardProgramSweeper from "./ProgramsSweeper";

const Program = () => {
    const servicesStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '16px',
        lineHeight: '24px',
        textAlign: 'left',
        color: '#6941C6',
    }
    const descriptionStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '36px',
        lineHeight: '44px',
        textAlign: 'left',
        letterSpacing: '-0.02em',
        color: '#101828',
    }
    const additionalDescriptionStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '20px',
        lineHeight: '30px',
        color: '#667085',
    }
  return (
    <div className="flex flex-col my-20">
        <div style={servicesStyle} className='mb-3'>Explore Programs</div>
        <div style={descriptionStyle} className='mb-5'>Our Most Popular Class</div>
        <div style={additionalDescriptionStyle}>Let's join our famous class, the knowledge provided will definitely be useful for you.</div>
        <CardProgramSweeper/>
        <SecondaryBtn style={{margin:'auto'}} text='Explore All Programs'/>
    </div>
  );
}

export default Program;