import TestimonyCard from './TestimonyCard'
import small_circle from './../../img/home/small_circle.svg';

const Testimony = () => {
  return (
    <div>
        <TestimonyCard/>
        <div className="flex justify-center gap-3 mt-5">
            <img src={small_circle} alt='img'/>
            <img src={small_circle} alt='img'/>
            <img src={small_circle} alt='img'/>
        </div>
    </div>
  )
}

export default Testimony