export default function TagLabel({text='Sample Btn', category, style={}, ...rest}){
    let textColor;
    let backgroundColor;
    switch(category){
        case 'research':
            textColor='#C11574';
            backgroundColor= '#FDF2FA';
            break;
        case 'uiux':
            textColor='#027A48';
            backgroundColor= '#ECFDF3';
            break;
        case 'uidesign':
            textColor='#026AA2';
            backgroundColor= '#F0F9FF';
            break;
        case 'programming':
            textColor='#6941C6';
            backgroundColor= '#F9F5FF';
            break;
        case 'development':
            textColor='#3538CD';
            backgroundColor= '#EEF4FF';
            break;   
        default:
            textColor='#101828';
            backgroundColor= '#FFFFFF';
            break;                
        

    }
    
    const labelStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '500',
        fontSize: '14px',
        lineHeight: '20px',
        textAlign: 'center',
        color: textColor,
        padding: '2px 10px',
        height: '24px',
        background: backgroundColor,
        borderRadius: '16px',
    }
    return(
        <button style={{...style,...labelStyle}} className='w-fit px-4' {...rest}>{text}</button>
    );
}