import CardTypeA from "./CardTypeA";
import CardTypeB from "./CardTypeB";
import ModelFrame from "./ModelFrame";
import board from './../../img/home/board.svg'

export default function HomeMainImage(){
    return(
        <div style={{minWidth:'610px', maxWidth:'750px', height:'582px', position:'relative'}}>
            <CardTypeA style={{position:'absolute', top:'192px'}}/>
            <CardTypeA img={board} boldTop={false} text1='Tutors' text2="250+" style={{position:'absolute', right:'20px', bottom:'40px'}}/>
            <CardTypeB style={{position:'absolute', right:'0', top:'0'}}/>
            <ModelFrame style={{position:'absolute', zIndex:'-10', left:'50%', transform:'translateX(-50%)'}}/>
        </div>
    );
}