//default img
import online_education from './../../img/home/online_education.svg';

export default function CardTypeA({img=online_education,text1='2K+',text2='Video Courses', boldTop=true, style={}}){
    const containerStyle = {
        background: '#F5F5F4',
        border: '0.765px solid #7F56D9',
        boxShadow: '0px 18.36px 38.25px rgba(0, 0, 0, 0.15)',
        borderRadius: '18.36px'
    }

    const boldStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '25px',
        color: '#101828',
    }

    const lightStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '15px',
        color: '#101828',
        opacity: '0.5',
    }
    return(
        <div className='flex h-24 items-center px-5 w-fit' style={{...containerStyle,...style}}>
            <div className='w-12 h-12 flex justify-center mr-6' style={{backgroundColor:'#7F56D9',borderRadius: '12.24px'}}>
                <img src={img} alt='img' style={{width:'31px'}}/>
            </div>
            <div>
                <div style={boldTop?boldStyle:lightStyle}>{text1}</div>
                <div style={boldTop?lightStyle:boldStyle}>{text2}</div>
            </div>
        </div>
    );
}