import Logo from "../header/Logo"
import FooterLink from "./FooterLink"
import MedsosLink from "./MedsosLink"

const Footer = () => {
    const descriptionStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '16px',
        lineHeight: '24px',
        color: '#EAECF0',
    }
    const copyRightStyle = {
        ...descriptionStyle, color:'#98A2B3'
    }
  return (
    <div className="flex pt-16 pb-12" style={{backgroundColor:'#101828'}}>
        <div className="w-full flex flex-col mx-auto gap-24" style={{maxWidth:'1280px'}}>
            <div className="w-full flex gap-16">
                <div>
                    <Logo textColor="#FFFFFF"/>
                    <div className="text-white mt-9" style={descriptionStyle}>Top learning experiences that create more talent in the world.</div>
                </div>
                <FooterLink/>
            </div>
            <div className="flex">
                <div className="text-white" style={copyRightStyle}>© 2022 Ed-Circle. All rights reserved.</div>
                <MedsosLink style={{marginLeft:'auto'}}/>
            </div>
        </div>
    </div>
  )
}

export default Footer