import defaultAvatar from './../../img/programe/Avatar1.jpg'

const TutorsName = ({img=defaultAvatar,name='Jane Cooper',yearEnroll='2001'}) => {
    const nameStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '14px',
        lineHeight: '20px',
        color: '#101828',
        marginLeft:'12px'
    }

    const enrollStyle = {
        ...nameStyle,fontWeight:'400',color:'#667085'
    }
  return (
    <div className='flex items-center'>
        <div className='flex justify-center rounded-full overflow-hidden' style={{width:'40px',height:'40px'}}>
            <img src={img} alt='img' style={{width:'40px'}}/>
        </div>
        <div>
            <div style={nameStyle}>{name}</div>
            <div style={enrollStyle}>{yearEnroll} Enrolled</div>
        </div>
    </div>
  )
}

export default TutorsName