import React from 'react'
import Logo from './../header/Logo'
import UserTestimonyProfile from './UserTestimonyProfile'

const TestimonyCard = ({testimony='Courses was fantastic! It is Master platform for those looking to start a new career, or need a refresher.', img, name, institution}) => {
    const testimonyStyle = {
        maxWidth:'1200px',
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '500',
        fontSize: '48px',
        lineHeight: '60px',
        textAlign: 'center',
        letterSpacing: '-0.02em',
        color: '#101828',
    }
  return (
    <div className='w-full flex flex-col items-center justify-center gap-9' style={{height: '546.29px',background: '#F9FAFB'}}>
        <Logo/>
        <div style={testimonyStyle}>{testimony}</div>
        <UserTestimonyProfile img={img} name={name} institution={institution}/>
    </div>
  )
}

export default TestimonyCard