import logo from './../../img/header/logo.svg';

export default function Logo({img=logo,text='Ed-Circle.', style={}, textColor='#101828', ...rest}){
    const textLogoStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '800',
        fontSize: '20px',
        lineHeight: '24px',
        letterSpacing: '0.02em',
        color: textColor
    }
    return(
        <div className='flex items-center cursor-pointer' {...rest} style={style}>
            <img src={logo} alt='img' width={34} style={{margin:"5px 10px"}}/>
            <div style={textLogoStyle}>{text}</div>
        </div>
    );
}