import React from 'react'
import TagLabel from '../low-level/TagLabel'

const tags = [
    <TagLabel text='Research' category='research'/>,
    <TagLabel text='UI UX' category='uiux'/>
]
const Caption = ({date='November 16, 2014', title='Three Pillars of User Delight', text='Delight can be experienced viscerally, behaviourally, and reflectively. A great design is ...',tag=tags, height=200, style={}}) => {
    const dateStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '14px',
        lineHeight: '20px',
        color: '#6941C6',
    }

    const titleStyle = {
        ...dateStyle, fontSize:'18px', lineHeight:'28px', color:'#101828'
    }

    const textStyle = {
        ...dateStyle, fontSize:'16px', lineHeight:'24px', color:'#667085', fontWeight:'400', maxHeight:((height/3).toString()+'px')
    }
    
  return (
    <div className='flex flex-col' style={{...style,height:(height.toString()+'px')}}>
        <div className='mb-3' style={dateStyle}>{date}</div>
        <div className='mb-2' style={titleStyle}>{title}</div>
        <div style={textStyle}>{text}</div>
        <div className='flex gap-2 mt-auto mb-3'>{tag}</div>
    </div>
  )
}

export default Caption