import TutorCard from "./TutorCard";
import avatar2 from './../../img/tutors/Avatar2.jpg';
import avatar3 from './../../img/tutors/Avatar3.jpg';
import avatar4 from './../../img/tutors/Avatar4.jpg';

const defaultcards = [
    <TutorCard/>,
    <TutorCard img={avatar2} name={'Courtney Henry'} expertise={'Director, Undergraduate Analytics and Planning'} experience={'Lead engineering teams at Figma, Pitch, and Protocol Labs.'}/>,
    <TutorCard img={avatar3} name={'Albert Flores'} expertise={'Career Educator'} experience={'Former PM for Linear, Lambda School, and On Deck.'}/>,
    <TutorCard img={avatar4} name={'Marvin McKinney'} expertise={'Co-op & Internships Program & Operations Manager'} experience={'Former frontend dev for Linear, Coinbase, and Postscript.'}/>   
]
const TutorSlider = ({cards=defaultcards}) => {
  return (
    <div className="flex justify-between">
        {cards}
    </div>
  )
}

export default TutorSlider