import TutorSlider from "./TutorSlider"

const Tutors = () => {
    const servicesStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '16px',
        lineHeight: '24px',
        textAlign: 'center',
        color: '#6941C6',
    }
    const descriptionStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '600',
        fontSize: '36px',
        lineHeight: '44px',
        textAlign: 'center',
        letterSpacing: '-0.02em',
        color: '#101828',
    }
    const additionalDescriptionStyle = {
        maxWidth:'768px',
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '20px',
        lineHeight: '30px',
        textAlign: 'center',
        color: '#667085',
    }
  return (
    <div className="flex flex-col my-20">
        <div style={servicesStyle} className='mb-3'>Tutors</div>
        <div style={descriptionStyle} className='mb-5'>Meet the Heroes</div>
        <div style={additionalDescriptionStyle} className='mx-auto mb-10'>On Ed-Circle, instructors from all over the world instruct millions of students. We offer the knowledge and abilities.</div>
        <TutorSlider/>
    </div>
  );
}

export default Tutors;