import web_dev from './../../img/services/web_dev.svg'
export default function CardServices({img=web_dev, title='Web Development', description='Classes in development that cover the most recent advancements in web.', color='#E0EAFF'}){
    const titleStyle={
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '24px',
        lineHeight: '150%,',
        letterSpacing: '0.02em',
        color: '#101828',
    }
    const containerStyle = {
        width: '373px',
        height: '243px',
        background:'#FFFFFF',
        border: '1px solid rgba(216, 216, 216, 0.4)',
        boxShadow: '0px 67.1144px 109.061px rgba(0, 0, 0, 0.05)',
        borderRadius: '12px',
    }

    const linkStyle = {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '500',
        fontSize: '18px',
        lineHeight: '140%',
        letterSpacing: '0.02em',
        color: '#7F56D9',
    }

    const descriptionStyle={
        fontfamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '400',
        fontSize: '16px',
        lineHeight: '180%',
        color: '#646464',
    }

    return(
        <div className='px-10 py-7 flex flex-col justify-between' style={containerStyle}>
            <div className='flex items-center gap-5'>
                <div className='w-12 h-12 flex justify-center' style={{backgroundColor:color,borderRadius: '12.24px'}}>
                    <img src={img} alt='img' style={{width:'24px'}}/>
                </div>
                <div style={titleStyle}>{title}</div>
            </div>
            <div style={descriptionStyle}>{description}</div>
            <div style={linkStyle}>Learn More {'>'}</div>
        </div>
    )
}